:root
{
    --appBackground : #E98EAD;
    --contColor : #FEA1BF;
    --tileBgColor : #FFC6D3;
    --blue : #3A4F7A;
    --activeTileBg : #25316D;
    --passiveTileBg : #393E46;
}
*
{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.todo-app
{
    padding: 2rem;
    background-color: var(--appBackground);
    width: fit-content;
    height: fit-content;
    min-width: 100vw;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items:  center;
    gap : 50px; 
}
.todo-cont
{
    padding: 2rem;
    background-color: var(--contColor);
    border-radius: 10px;
    height: fit-content;
    width: fit-content;  
}

.todo-list-cont
{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 30px;
    animation: fade 400ms ease-in-out;
}

.todo-list-tiles
{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
}

.todo-tile
{

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    gap: 15px;
    background-color: var(--activeTileBg);
    border-radius: 10px;
    width: 150px;
    height: 200px;
    padding: 10px;
    color: white;
    transition: all ease-in-out 200ms;
    transition-property: color,background-color,filter,transform;
}  
.tile-passive
{
    background-color: var(--passiveTileBg);
}

.add-task
{
    font-size: 30px;
    cursor: pointer;
}
.todo-tile:hover
{
    filter: hue-rotate(0deg);
    transform: scale(1.1);
}
.todo-tile-top:hover,.btn:hover
{
    background-color: rgba(0, 0, 0, 0.503);
    border-radius: 20px;
}
.todo-tile-top
{
    height: 100%;
    width: 100%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    gap: 10px;
    cursor: pointer;
    transition: filter,transform ease-in-out 200ms;
}
.todo-tile-bottom
{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    
}
.btn
{
    padding: 10px;
    font-size: 20px;
    display: inline;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}
.todo-tile input[type="checkbox"] 
{
    display: none;    
}
.todo-edit-cont
{
    min-width: 60vw;
    min-height: 60vh;
    display: grid;
    row-gap: 20px;
    column-gap: 30px;
    grid-template-columns: 80px auto 80px;
    grid-template-rows: 50px auto;
    grid-template-areas: 
    "home title save"
    "content  content content"
    ;

    animation: fade 400ms ease-in-out ;

}

textarea
{
    padding: 10px;
    background-color: #ffc6d378;
    border-radius: 10px;
    width: 250px;
    font-size: 20px;
    border-radius: 6px;
    border: 0;
    resize: none;
    width: 100%;
    transition: background-color ease-in-out 200ms;
    box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
textarea:focus
{
    outline: none;
    border-radius: 15px;
    background-color: #FFC6D3;
}

.task-title
{
    grid-area: title;
    height: 50px;
    width: 100%;
    min-width: 100px;
    text-align: center;
    font-size: 25px;
    font-weight: 400;
}
.task-content
{
    grid-area: content;
}
.save-btn
{   
    grid-area: save;
}
.home-btn
{
    grid-area: home;
}

@keyframes fade {
    0%
    {
        transform: scale(1.2);
        opacity: 0;
    }
    100%
    {
        transform: scale(1);
        opacity: 1;
    }
}

@media screen and (max-width: 600px)  {
    .todo-edit-cont
    {
        grid-template-columns: auto auto auto;
        grid-template-rows: 50px 50px auto;
        grid-template-areas: 
        "home . save"
        ". title ."
        "content  content content"
        ;
    }
}
